export default {
  props: {
    inputData: {
      type: Object,
      default: () => {},
    },
    messages: {
      type: Object,
      default: () => {},
    },
    showError: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    errorText() {
      if (!this.inputData.email) {
        return this.messages.email;
      }
      if (!this.inputData.required) {
        return this.messages.required;
      }
      if (!this.inputData.maxLength) {
        return this.messages.min;
      }
      if (!this.inputData.maxLength) {
        return this.messages.max;
      }
      return null;
    },
  },
};
