import { mapActions } from "vuex";
import StyledLink from "./StyledLink.vue";
export default {
  components: { StyledLink },
  name: "ButtonsTray",
  props: {
    buttons: {
      type: [Array, Object],
      default: null,
    },
    invent: {
      default: null,
    },
  },
  methods: {
    ...mapActions(["setMenuState", "setMenuData", "setFormId"]),
    formOpen(data, id) {
      console.log(data);
      if (data) {
        this.setMenuState(true);
        this.setFormId(id);
        this.setMenuData(data);
      }
    },
  },
  data() {
    return {};
  },
};
