import ButtonsTray from "../common/ButtonsTray.vue";
// import StyledLink from "../common/StyledLink.vue";
export default {
  components: { ButtonsTray },
  name: "FirstScreen",
  data() {
    return {};
  },
  computed: {
    hasImg() {
      return Boolean(this.propsData.image);
    },
    type() {
      switch (this.propsData.widget_type) {
        case "type_1":
          return "1";
        case "type_2":
          return "2";
        case "type_3":
          return "3";
        default:
          return "1";
      }
    },
    classList() {
      let classlist = "";
      switch (this.type) {
        case "1":
          classlist += "type1";
          break;
        case "2":
          classlist += "type2";
          break;
        case "3":
          classlist += "type3";
          break;
        default:
          classlist += "";
          break;
      }
      if (this.propsData.image) classlist += " hasImg";
      return classlist;
    },
    img() {
      if (this.propsData.image && +this.propsData.with_image_fon) {
        return {
          backgroundImage: `url('${this.path(this.propsData.image)}')`,
        };
      } else {
        return null;
      }
    },
  },
};
