import { adapter } from "@/utils.js";
import SectionComponent from "../components/common/SectionComponent.vue";
export default {
  name: "Landing",
  components: {
    SectionComponent,
  },
  data() {
    return {
      loaded: false,

      constructorComponents: [],
      model: null,
      translate: null,
    };
  },
  computed: {},
  created() {
    let url;
    let props;
    console.log(this.$route.query.prevw);
    switch (this.$route.name) {
      case "home":
        url = "/api/get-main-page";
        props = {
          lang: this.$i18n.locale,
        };
        break;
      case "landing":
        url = "/api/landing/get-by-slug";

        props = {
          lang: this.$i18n.locale,
          slug: this.$route.params.slug,
          prevw: this.$route.query.prevw ? this.$route.query.prevw : null,
        };
        break;
      default:
        url = "/api/page/get-by-slug";
        props = {
          lang: this.$i18n.locale,
          slug: this.$route.params.slug,
        };
        break;
    }
    adapter
      .post(url, props, this)
      .then((response) => {
        if (response.contacts) {
          this.constructorComponents.push({
            component: "contactsComponent",
            content: {
              contacts: response.contacts,
              api_key: response.maps_api_key,
            },
            visibility: "1",
          });
          this.constructorComponents = [
            ...this.constructorComponents,
            ...response.constructor,
          ];
        } else {
          this.constructorComponents = response.constructor;
        }
        this.model = response.model;
        this.translate = response.translate;
        this.setMeta();
        this.loaded = true;
      })
      .catch((e) => {
        this.handleError(e);
      });
  },
};
